@import "./_variables";
@import "./_mixins";

.introList {
    counter-increment: step;

    &+& {
        position: relative;
        margin-top: 80px;
        padding-top: 80px;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 458px;
            height: 2px;
            background-image: linear-gradient(to left, #fdce63, #3abd67, #0094ac);
        }
    }

    &-pic {
        position: relative;

        img {
            width: 100%;
            border-radius: 0 50px 0 0;
        }

        &::after {
            content: counter(step, decimal);
            position: absolute;
            bottom: -30px;
            left: -20px;
            box-sizing: border-box;
            width: 50px;
            height: 50px;
            padding: 4.5px;
            font-size: 35px;
            line-height: 1;
            color: #fff;
            text-align: center;
            border-radius: 0 0 20px 0;
            background-image: linear-gradient(135deg, #fdce63, #3abd67, #0094ac);
        }
    }

    &-content {
        margin-top: 50px;
        padding: 0 10px;
    }

    &-title {
        font-size: 32px;
        font-weight: bold;
        color: #449ebd;
    }

    &-text {
        margin-top: 20px;

        p {
            font-size: 21px;
            line-height: 1.8;
            color: #8b8b8b;
        }
    }

    &-action {
        margin-top: 20px;
        text-align: center;
    }

    &.-hor {
        display: flex;
        align-items: flex-start;
        margin-left: -180px;
        margin-right: -180px;
    }

    &.-hor & {
        &-pic {
            flex: 0 0 auto;
            display: flex;
            justify-content: flex-end;
            width: 49.5%;
            height: 375px;

            img {
                width: auto;
                height: 100%;
                border-radius: 0 0 50px 0;
            }

            &::after {
                bottom: auto;
                left: auto;
                top: -30px;
                right: -30px;
            }
        }

        &-content {
            flex: 1;
            margin-top: 0;
            padding: 30px;
        }
    }

    &.-gradient & {
        &-pic {
            padding-right: 80px;

            img {
                border-radius: 0 0 50px 0;
            }

            &::after {
                top: -30px;
                bottom: auto;
            }
        }

        &-content {
            position: relative;
            margin-top: 40px;
            padding: 0 30px 35px 10px;
        }

        &-deco {
            content: '';
            position: absolute;
            top: -120px;
            right: 0;
            width: calc(100% + 50vw - 50%);
            height: calc(100% + 120px);
            background-image: linear-gradient(to left, #fdce63, #3abd67, #0094ac);
            z-index: -1;
        }

        &-title {
            color: #fff
        }

        &-text {
            p {
                color: #000;
            }
        }
    }

    @include breakpoint-down('desktop') {
        &-pic {
            &::after {
                bottom: -25px;
                left: -15px;
                width: 42px;
                height: 42px;
                font-size: 28px;
                border-radius: 0 0 15px 0;
            }
        }

        &-content {
            margin-top: 40px;
        }

        &-title {
            font-size: 28px;
        }

        &-text {
            margin-top: 15px;

            p {
                font-size: 18px;
            }
        }

        &-action {
            margin-top: 15px;
        }

        &.-hor & {
            &-pic {
                height: 305px;

                &::after {
                    top: -25px;
                    right: -25px;
                }
            }

            &-content {
                flex: 1;
                margin-top: 0;
                padding: 30px;
            }
        }

        &.-gradient & {
            &-pic {
                padding-right: 60px;

                &::after {
                    top: -25px;
                }
            }

            &-content {
                margin-top: 30px;
                padding: 0 25px 25px 10px;
            }

            &-deco {
                top: -100px;
                height: calc(100% + 100px);
            }
        }
    }

    @include breakpoint-down('laptop') {
        &.-hor {
            margin-left: -100px;
            margin-right: -100px;
        }
    }

    @include breakpoint-down('tablet') {
        &+& {
            margin-top: 65px;
            padding-top: 65px;

            &::before {
                width: 360px;
            }
        }

        &-pic {
            &::after {
                bottom: -20px;
                left: -10px;
                width: 35px;
                height: 35px;
                font-size: 22px;
            }
        }

        &-content {
            margin-top: 35px;
        }

        &-title {
            font-size: 24px;
        }

        &-text {
            p {
                font-size: 16px;
            }
        }

        &.-hor {
            margin-left: -20px;
            margin-right: -20px;
        }

        &.-hor & {
            &-pic {
                height: 255px;

                &::after {
                    top: -20px;
                    right: -20px;
                }
            }

            &-content {
                padding: 20px;
            }
        }

        &.-gradient & {
            &-pic {
                padding-right: 50px;

                &::after {
                    top: -20px;
                }
            }

            &-content {
                margin-top: 25px;
                padding: 0 20px 20px 10px;
            }
        }
    }

    @include breakpoint-down('mobile') {
        &+& {
            margin-top: 50px;
            padding-top: 50px;

            &::before {
                width: 280px;
            }
        }

        &-pic {
            img {
                border-radius: 0 30px 0 0;
            }

            &::after {
                border-radius: 0 0 10px 0;
            }
        }

        &-content {
            margin-top: 30px;
        }

        &.-hor {
            display: block;
            margin-left: 0;
            margin-right: 0;
        }

        &.-hor & {
            &-pic {
                width: 100%;
                height: auto;

                img {
                    width: 100%;
                    height: auto;
                    border-radius: 0 0 30px 0;
                }

                &::after {
                    right: -10px;
                }
            }

            &-content {
                margin-top: 25px;
                padding: 0 10px;
            }
        }

        &.-gradient & {
            &-pic {
                padding-right: 20px;

                img {
                    border-radius: 0 0 30px 0;
                }
            }

            &-content {
                padding: 0 20px 20px 10px;
            }

            &-deco {
                top: -80px;
                height: calc(100% + 80px);
            }
        }
    }
}

.introContent {
    &+& {
        margin-top: 100px;
    }

    &-title {
        margin-bottom: 25px;
        font-size: 32px;
        font-weight: bold;
        color: #449ebd;

        &.-gn {
            color: #36bc67;
        }
    }

    .introContent &-title {
        font-size: 30px;
    }

    &-wording {
        font-size: 21px;
        line-height: 1.8;
        color: #8b8b8b;

        ol {
            margin: 0;
            padding-left: 1.2em;
        }
    }

    &-pic {
        margin-top: 35px;
        text-align: center;

        img {
            width: 100%;
            border-radius: 0 0 50px 0;

            &.-mb {
                display: none;
            }
        }
    }

    &-box {
        margin-top: 45px;
        padding: 30px 40px;
        box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
        border-radius: 50px 0 0 0;
        background-color: #fdfffe;
    }

    @include breakpoint-down('desktop') {
        &+& {
            margin-top: 80px;
        }

        &-title {
            margin-bottom: 15px;
            font-size: 28px;
        }

        .introContent &-title {
            font-size: 26px;
        }

        &-wording {
            font-size: 18px;
        }

        &-pic {
            margin-top: 25px;
        }

        &-box {
            margin-top: 35px;
            padding: 20px 30px;
        }
    }

    @include breakpoint-down('tablet') {
        &+& {
            margin-top: 60px;
        }

        &-title {
            margin-bottom: 10px;
            font-size: 24px;
        }

        .introContent &-title {
            font-size: 22px;
        }

        &-wording {
            font-size: 16px;
        }

        &-pic {
            margin-top: 20px;
        }

        &-box {
            margin-top: 30px;
            padding: 10px 20px;
            border-radius: 30px 0 0 0;
        }
    }

    @include breakpoint-down('mobile') {
        .introContent &-title {
            font-size: 20px;
        }

        &-pic {
            img {
                &.-pc {
                    display: none;
                }

                &.-mb {
                    display: inline-block;

                    &.-process {
                        max-width: 300px;
                    }
                }
            }
        }
    }
}

.feature {
    display: flex;
    align-items: center;
    padding: 10px 50px;

    &-title {
        flex: 0 0 auto;
        font-size: 32px;
        font-weight: bold;
        text-align: center;
        color: #449ebd;

        img {
            width: 125px;
            margin-top: 10px;
        }
    }

    &-content {
        flex: 1;
        display: flex;
        flex-wrap: wrap;
        margin-left: 80px;
        padding-left: 0;
        font-size: 21px;
        line-height: 1.8;
        color: #8b8b8b;

        li {
            flex: 0 0 auto;
            box-sizing: border-box;
            width: calc(50% - 1.8em);
            margin-left: 1.8em;

            &.-note {
                width: calc(100% - 0.5em);
                margin-top: 20px;
                margin-left: 0.5em;
                font-size: 18px;
                list-style: none;
            }
        }
    }

    @include breakpoint-down('desktop') {
        padding: 5px 40px;

        &-title {
            font-size: 24px;

            img {
                width: 105px;
            }
        }

        &-content {
            margin-left: 60px;
            font-size: 18px;

            li {
                &.-note {
                    margin-top: 15px;
                    font-size: 16px;
                }
            }
        }
    }

    @include breakpoint-down('tablet') {
        padding: 0 20px;

        &-title {
            font-size: 20px;

            img {
                width: 90px;
            }
        }

        &-content {
            margin-left: 35px;
            font-size: 16px;

            li {
                &.-note {
                    margin-top: 10px;
                    font-size: 14px;
                }
            }
        }
    }

    @include breakpoint-down('mobile') {
        display: block;
        padding: 10px 0;

        &-content {
            margin-left: -5px;
            margin-right: -5px;

            li {
                width: calc(50% - 1.3em);
                margin-left: 1.3em;
                font-size: 14px;

                &.-note {
                    margin-left: 0;
                    font-size: 13px;
                }
            }
        }
    }
}

.ball {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;

    &-item {
        position: relative;
        flex: 0 0 auto;
        width: calc(52% / 3);
        margin: 8%;
        padding-bottom: calc(52% / 3);
        border-radius: 50%;

        &.-rd {
            background-color: #fff1f7;
            box-shadow: 0 0 40px 40px #fff1f7;
        }

        &.-yl {
            background-color: #fcedc8;
            box-shadow: 0 0 40px 40px #fcedc8;
        }

        &.-brn {
            background-color: #f1e9d4;
            box-shadow: 0 0 40px 40px #f1e9d4;
        }

        &.-gn {
            background-color: #e3f5ea;
            box-shadow: 0 0 40px 40px #e3f5ea;
        }

        &.-bu {
            background-color: #d6eef2;
            box-shadow: 0 0 40px 40px #d6eef2;
        }
    }

    &-wrap {
        position: absolute;
        top: -5%;
        left: 50%;
        transform: translateX(-50%);
    }

    &-icon {
        img {
            width: 102px;
        }
    }

    &-title {
        font-size: 28px;
        font-weight: bold;
        color: #000;
    }

    &-text {
        font-size: 21px;
        line-height: 1.5;
        color: #272727;
        white-space: nowrap;
    }

    &-note {
        width: 100%;
    }

    @include breakpoint-down('desktop') {
        &-wrap {
            top: 0;
        }

        &-icon {
            img {
                width: 90px;
            }
        }

        &-title {
            font-size: 24px;
        }

        &-text {
            font-size: 18px;
        }
    }

    @include breakpoint-down('desktop') {
        &-wrap {
            top: -5%;
        }

        &-icon {
            img {
                width: 70px;
            }
        }

        &-title {
            font-size: 20px;
        }

        &-text {
            font-size: 16px;
        }

        &-note {
            margin-top: 20px;
        }
    }

    @include breakpoint-down('mobile') {
        max-width: 550px;
        margin: 0 auto;

        &-item {
            width: 107px;
            margin: 30px;
            padding-bottom: 107px;

            &.-rd {
                box-shadow: 0 0 20px 20px #fff1f7;
            }

            &.-yl {
                box-shadow: 0 0 20px 20px #fcedc8;
            }

            &.-brn {
                box-shadow: 0 0 20px 20px #f1e9d4;
            }

            &.-gn {
                box-shadow: 0 0 20px 20px #e3f5ea;
            }

            &.-bu {
                box-shadow: 0 0 20px 20px #d6eef2;
            }
        }

        &-icon {
            img {
                width: 60px;
            }
        }

        &-title {
            font-size: 19px;
        }

        &-text {
            font-size: 15px;
        }

        &-note {
            font-size: 14px;
        }
    }
}

@import "./_utilities";